import { Formik } from 'formik';
import {
    SignInFormStyled, InputContainer, InputStyled, LabelStyled,
    ErrorMessage, SignInBtn, FormHeader, FormText, RegistrationBtn, RegistrationContainer
} from './SignInForm.styled';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { auth } from '../../firebase';

export const SignInForm = ({ toggleTypeOfForm, closeModal }) => {

    const initialValues = {
        email: '',
        password: '',
  };

  const enterUser = ({ email, password }, { resetForm }) => {
    signInWithEmailAndPassword(auth, email, password)
      .then(credentials => {
        closeModal();
        resetForm();
      })
      .catch(e => console.error(e));
    };

    function validateEmail(value) {
       let error;
       if (!value) {
         error = '\' *';
       } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
         error = '   *';
       }
       return error;
    };
    
    function validatePassword(value) {
       let error;
       if (!value) {
         error = '\' *';
       } else if (!/(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}/.test(value)) {
         error = '    ,        8   *';
       }
       return error;
    };

    return (
        <>
            <Formik initialValues={initialValues} onSubmit={enterUser}>
                {({ errors, touched }) => (
                    <>
                        <FormHeader>  </FormHeader>
                        <FormText>  !   </FormText>
                        <SignInFormStyled>
                            <InputContainer>
                                <InputStyled
                                  id="email"
                                  type="email"
                                  name="email"
                                  autoComplete="off"
                                  placeholder=" "
                                  validate={validateEmail}
                                />
                                <LabelStyled htmlFor="email"> </LabelStyled>
                                {errors.email && touched.email && <ErrorMessage>{errors.email}</ErrorMessage>}
                            </InputContainer>
                            
                            <InputContainer>
                                <InputStyled
                                  id="password"
                                  type="password"
                                  name="password"
                                  autoComplete="off"
                                  placeholder=" "
                                  validate={validatePassword}
                                />
                                <LabelStyled htmlFor="password"></LabelStyled>
                                {errors.password && touched.password && <ErrorMessage>{errors.password}</ErrorMessage>}
                            </InputContainer>

                            <SignInBtn type="submit"></SignInBtn>
                            <RegistrationContainer>
                                <p>  ?</p>
                                <RegistrationBtn type="button" onClick={toggleTypeOfForm}></RegistrationBtn>
                            </RegistrationContainer>
                        </SignInFormStyled>
                    </>
                )}
            </Formik>
        </>
    )
}